import React, {useEffect, useState} from "react";
import {AppBar, Button, CardMedia, Container, styled, Switch, Toolbar, Typography,} from "@mui/material";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import {useLocation} from "react-router-dom";
import GlassEffect from "../theme/GlassEffect";

const StyledToolbar = styled(Toolbar)(({theme}) => ({
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    padding: "8px 12px",
    flexShrink: 0,
}));

const HeaderComponent: React.FC<{ mode: string; setMode: (mode: string) => void }> = ({mode, setMode}) => {
    const [pageName, setPageName] = React.useState<string | undefined>(undefined);
    const [, setHeaderHeight] = useState<number>(0);
    const toggleTheme = () => setMode(mode === "light" ? "dark" : "light");

    const location = useLocation();

    useEffect(() => {
        let currentPage: string | undefined = location.pathname;
        if (currentPage === "/") currentPage = "Home";
        else currentPage = currentPage.split("/").pop();

        if (currentPage) currentPage = currentPage.charAt(0).toUpperCase() + currentPage.slice(1);

        setPageName(currentPage?.replaceAll("-", " "));
    }, [location]);

    useEffect(() => {
        const header = document.getElementById("header-component");
        if (header) setHeaderHeight(header.offsetHeight);
    }, []);

    return (
        <>
            <AppBar
                id="header-component"
                position="fixed"
                sx={{
                    boxShadow: "none",
                    bgcolor: "transparent",
                    backgroundImage: 'none',
                    zIndex: 1200,
                }}
                color="default"
            >
                <Container maxWidth="lg">
                    <GlassEffect>
                        <StyledToolbar variant="dense" disableGutters>
                            <CardMedia
                                component="img"
                                image="logo512.png"
                                alt="projectsincluded logo"
                                sx={{
                                    width: {xs: "20%", sm: "7%", md: "5%", lg: "5%", xl: "5%"},
                                    height: "auto"
                                }}
                            />
                            <Typography variant="h6" component="div" sx={{flexGrow: 1}} color="textSecondary">
                                {pageName}
                            </Typography>
                            <Button color="inherit" variant="text" href="/">
                                <Typography variant="inherit" component="div" sx={{flexGrow: 1}} color="textSecondary">
                                    Home
                                </Typography>
                            </Button>
                            <Button color="inherit" variant="text" href="/about-us">
                                <Typography variant="inherit" component="div" sx={{flexGrow: 1}} color="textSecondary">
                                    About
                                </Typography>
                            </Button>
                            <Switch
                                checked={mode === "dark"}
                                onChange={toggleTheme}
                                inputProps={{"aria-label": "controlled"}}
                                icon={<Brightness7Icon sx={{fontSize: 24, position: "relative", bottom: "2px"}}/>}
                                checkedIcon={<Brightness4Icon
                                    sx={{fontSize: 24, position: "relative", bottom: "2px"}}/>}
                            />
                        </StyledToolbar>
                    </GlassEffect>
                </Container>
            </AppBar>
        </>
    );
};

export default HeaderComponent;